<template>  
  <Navbar @change_page="change_page"/>
  <Notion v-if="stage=='live'"/>
  <NotionArchive v-if="stage=='archive'" />
</template>

<script>

import {ref} from "vue";

import Navbar from './components/Navbar.vue'
import Notion from './components/Notion.vue'
import NotionArchive from './components/NotionArchive.vue'
export default {
  name: 'App',
  provide:{
    API:'http://localhost:8000'
  },
  components:{
    Navbar,
    Notion,
    NotionArchive
},setup(){

    const stage=ref("live");

    function change_page(page){
      stage.value = page;
    }

    return {
      stage,
      change_page
    }
  }
}
</script>

<style>
 body{
  background: #000;
 }
</style>
